'use client';

import { Clone, useGLTF } from '@react-three/drei';
import React, { Suspense, memo, useEffect, useMemo } from 'react';
import type { Mesh } from 'three';
import type { SessionInfo } from '@/types/logs';
import { useAssetAvailable } from '@/hooks/useAssetAvailable';

export type PigAgentProps = {
  session: SessionInfo;
  position: [number, number, number];
  selected?: boolean;
  onSelect?: (sessionId: string) => void;
};

class ModelErrorBoundary extends React.Component<
  { fallback: React.ReactNode },
  { hasError: boolean }
> {
  constructor(props: { fallback: React.ReactNode }) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  render() {
    if (this.state.hasError) {
      return this.props.fallback;
    }
    return this.props.children;
  }
}

const PigModel = ({ scale }: { scale: number }) => {
  const { scene } = useGLTF('/models/pig.glb');

  useEffect(() => {
    scene.traverse((child) => {
      if ((child as Mesh).isMesh) {
        const mesh = child as Mesh;
        mesh.castShadow = true;
        mesh.receiveShadow = true;
      }
    });
  }, [scene]);

  return <Clone object={scene} scale={scale} />;
};

const PigPlaceholder = ({ scale }: { scale: number }) => (
  // TODO: 放置 pig.glb 后替换占位猪模型。
  <group scale={scale}>
    <mesh position={[0, 0.4, 0]}>
      <sphereGeometry args={[0.52, 26, 20]} />
      <meshStandardMaterial color="#f1b9b5" roughness={0.6} />
    </mesh>
    <mesh position={[0, 0.16, 0.6]}>
      <sphereGeometry args={[0.26, 20, 16]} />
      <meshStandardMaterial color="#e9a9a4" roughness={0.7} />
    </mesh>
    <mesh position={[0.18, 0.1, 0.78]}>
      <sphereGeometry args={[0.055, 12, 10]} />
      <meshStandardMaterial color="#2a2422" />
    </mesh>
    <mesh position={[-0.18, 0.1, 0.78]}>
      <sphereGeometry args={[0.055, 12, 10]} />
      <meshStandardMaterial color="#2a2422" />
    </mesh>
  </group>
);

const PigAgent = memo(({ session, position, selected, onSelect }: PigAgentProps) => {
  const baseScale = session.kind === 'main' ? 1.2 : 0.8;
  const scale = selected ? baseScale * 1.08 : baseScale;
  const ringColor = useMemo(() => (selected ? '#ffd27a' : '#ffffff'), [selected]);
  const fallback = <PigPlaceholder scale={scale} />;
  const assetStatus = useAssetAvailable('/models/pig.glb');
  const canRenderModel = assetStatus === 'available';

  return (
    <group
      position={position}
      onPointerDown={(event) => {
        event.stopPropagation();
        onSelect?.(session.id);
      }}
    >
      {selected ? (
        <mesh rotation={[-Math.PI / 2, 0, 0]} position={[0, 0.06, 0]}>
          <ringGeometry args={[0.75, 1.05, 40]} />
          <meshStandardMaterial color={ringColor} emissive="#ffcc6b" emissiveIntensity={0.35} />
        </mesh>
      ) : null}
      {canRenderModel ? (
        <ModelErrorBoundary fallback={fallback}>
          <Suspense fallback={fallback}>
            <PigModel scale={scale} />
          </Suspense>
        </ModelErrorBoundary>
      ) : (
        fallback
      )}
    </group>
  );
});

PigAgent.displayName = 'PigAgent';

export default PigAgent;
