'use client';

import { useEffect, useState } from 'react';

export type AssetStatus = 'unknown' | 'available' | 'missing';

export const useAssetAvailable = (url: string): AssetStatus => {
  const [status, setStatus] = useState<AssetStatus>('unknown');

  useEffect(() => {
    let active = true;

    const check = async () => {
      try {
        const response = await fetch(url, {
          method: 'GET',
          cache: 'no-store',
          headers: { Range: 'bytes=0-0' },
        });
        if (active) setStatus(response.ok ? 'available' : 'missing');
      } catch {
        if (active) setStatus('missing');
      }
    };

    void check();

    return () => {
      active = false;
    };
  }, [url]);

  return status;
};
