'use client';

import React, { Suspense, useEffect } from 'react';
import { useGLTF } from '@react-three/drei';
import type { Mesh } from 'three';
import { useAssetAvailable } from '@/hooks/useAssetAvailable';

export type FarmerProps = {
  position?: [number, number, number];
  scale?: number;
  rotation?: [number, number, number];
};

class ModelErrorBoundary extends React.Component<
  { fallback: React.ReactNode },
  { hasError: boolean }
> {
  constructor(props: { fallback: React.ReactNode }) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError() {
    return { hasError: true };
  }

  render() {
    if (this.state.hasError) {
      return this.props.fallback;
    }
    return this.props.children;
  }
}

const FarmerModel = ({
  position,
  scale,
  rotation,
}: Required<FarmerProps>) => {
  const { scene } = useGLTF('/models/farmer.glb');
  useEffect(() => {
    scene.traverse((child) => {
      if ((child as Mesh).isMesh) {
        const mesh = child as Mesh;
        mesh.castShadow = true;
        mesh.receiveShadow = true;
      }
    });
  }, [scene]);
  return (
    <primitive object={scene} position={position} scale={scale} rotation={rotation} />
  );
};

const FarmerPlaceholder = ({
  position,
  scale,
  rotation,
}: Required<FarmerProps>) => (
  // TODO: 若未放置 farmer.glb，占位几何体会显示。
  <mesh position={position} rotation={rotation} scale={scale}>
    <cylinderGeometry args={[0.4, 0.5, 1.6, 18]} />
    <meshStandardMaterial color="#f1b67a" roughness={0.7} />
  </mesh>
);

const Farmer = ({
  position = [6.5, 0.8, 6.5],
  scale = 1,
  rotation = [0, -Math.PI / 4, 0],
}: FarmerProps) => {
  const fallback = (
    <FarmerPlaceholder position={position} scale={scale} rotation={rotation} />
  );
  const assetStatus = useAssetAvailable('/models/farmer.glb');
  const canRenderModel = assetStatus === 'available';

  return (
    <>
      {canRenderModel ? (
        <ModelErrorBoundary fallback={fallback}>
          <Suspense fallback={fallback}>
            <FarmerModel position={position} scale={scale} rotation={rotation} />
          </Suspense>
        </ModelErrorBoundary>
      ) : (
        fallback
      )}
    </>
  );
};

export default Farmer;
