'use client';

import { Canvas } from '@react-three/fiber';
import { Environment } from '@react-three/drei';
import { Suspense, useMemo } from 'react';
import type { SessionInfo } from '@/types/logs';
import PigAgent from '@/components/PigAgent';
import Farmer from '@/components/Farmer';
import { useAssetAvailable } from '@/hooks/useAssetAvailable';

export type FarmSceneProps = {
  sessions: SessionInfo[];
  selectedSessionId: string | null;
  onSelectSession: (sessionId: string | null) => void;
};

const createGridPositions = (count: number) => {
  if (count === 0) return [] as [number, number, number][];
  const gridSize = Math.ceil(Math.sqrt(count));
  const spacing = 3.2;
  const offset = (gridSize - 1) / 2;

  return Array.from({ length: count }, (_, index) => {
    const row = Math.floor(index / gridSize);
    const col = index % gridSize;
    const x = (col - offset) * spacing;
    const z = (row - offset) * spacing;
    return [x, 0, z] as [number, number, number];
  });
};

const FarmScene = ({ sessions, selectedSessionId, onSelectSession }: FarmSceneProps) => {
  const pigs = useMemo(() => sessions.slice(0, 30), [sessions]);
  const positions = useMemo(() => createGridPositions(pigs.length), [pigs.length]);
  const pigModelAvailable = useAssetAvailable('/models/pig.glb') === 'available';
  const farmerModelAvailable = useAssetAvailable('/models/farmer.glb') === 'available';

  return (
    <Canvas
      shadows
      dpr={[1, 2]}
      camera={{ position: [0, 9, 14], fov: 45 }}
      tabIndex={0}
      aria-label="3D 农场场景"
      onPointerMissed={() => onSelectSession(null)}
      onKeyDown={(event) => {
        if (!pigs.length) return;
        const currentIndex = selectedSessionId
          ? pigs.findIndex((session) => session.id === selectedSessionId)
          : -1;
        const nextIndex = (indexDelta: number) => {
          const start = currentIndex === -1 ? 0 : currentIndex;
          return (start + indexDelta + pigs.length) % pigs.length;
        };

        if (event.key === 'ArrowRight' || event.key === 'ArrowDown') {
          event.preventDefault();
          const next = pigs[nextIndex(1)];
          onSelectSession(next?.id ?? null);
        }

        if (event.key === 'ArrowLeft' || event.key === 'ArrowUp') {
          event.preventDefault();
          const next = pigs[nextIndex(-1)];
          onSelectSession(next?.id ?? null);
        }

        if (event.key === 'Escape') {
          event.preventDefault();
          onSelectSession(null);
        }
      }}
    >
      <color attach="background" args={['#cfe0b7']} />
      <fog attach="fog" args={['#cfe0b7', 18, 42]} />

      <ambientLight intensity={0.6} />
      <directionalLight
        position={[8, 12, 6]}
        intensity={1.1}
        castShadow
        shadow-mapSize-width={2048}
        shadow-mapSize-height={2048}
      />

      <mesh rotation={[-Math.PI / 2, 0, 0]} receiveShadow>
        <planeGeometry args={[70, 70]} />
        <meshStandardMaterial color="#9fc27b" roughness={0.9} />
      </mesh>

      <mesh rotation={[-Math.PI / 2, 0, 0]} position={[0, 0.01, 0]}>
        <circleGeometry args={[18, 48]} />
        <meshStandardMaterial color="#a9d28c" roughness={0.8} />
      </mesh>

      <Suspense fallback={null}>
        <Environment preset="sunset" />
        {pigs.map((session, index) => (
          <PigAgent
            key={session.id}
            session={session}
            position={positions[index] ?? [0, 0, 0]}
            selected={session.id === selectedSessionId}
            onSelect={onSelectSession}
            renderModel={pigModelAvailable}
          />
        ))}
        <Farmer renderModel={farmerModelAvailable} />
      </Suspense>
    </Canvas>
  );
};

export default FarmScene;
